.gallery__list {
  overflow: hidden;
}

.gallery__list:not(.swiper-container-initialized) {
  padding-right: 0;
}

.gallery__list:not(.swiper-container-initialized) .gallery__list-inner {
  flex-wrap: wrap;
}

.gallery__list:not(.swiper-container-initialized) .gallery__item {
  margin: 0 15px 15px 0;
}

.gallery__list-inner {
  display: flex;
}

.gallery__image a {
  display: block;
  border: none;
  background-color: #eaeaea;
}

.gallery__image a:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transition: all 0.25s;
  background-color: transparent;
}

.gallery__image a:after {
  content: '\f00e';
  font-family: "Font Awesome 5 Pro";
  font-weight: 100;
  font-size: 30px;
  color: #ffffff;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transition: all 0.25s;
  opacity: 0;
  visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.gallery__image a:hover:before {
  background-color: rgba(51, 51, 51, 0.6);
}

.gallery__image a:hover:after {
  opacity: 1;
  visibility: visible;
}

.gallery__image img {
  display: block;
}

.gallery__dots {
  display: none;
  margin-top: 30px;
}

@media only screen and (min-width: 992px) {
  .gallery__list-inner {
    flex-wrap: wrap;
    margin-bottom: -15px;
  }
}

@media only screen and (min-width: 1200px) {
  .gallery__list:not(.swiper-container-initialized) .gallery__item {
    width: 281px;
  }

  .gallery__list:not(.swiper-container-initialized) .gallery__item:nth-child(4n) {
    margin-right: 0;
  }

  .gallery__image a {
    height: 198px;
  }
}

@media only screen and (max-width: 991px) {
  .gallery__list {
    padding-right: 100px;
  }

  .gallery__image a {
    pointer-events: none;
  }
}

@media only screen and (max-width: 767px) {
  .gallery__container {
    padding: 0;
    width: 100vw;
  }

  .gallery__list:not(.swiper-container-initialized) .gallery__item {
    width: calc(50% - 8px);
  }

  .gallery__list:not(.swiper-container-initialized) .gallery__item:nth-child(2n) {
    margin-right: 0;
  }

  .gallery__list:not(.swiper-container-initialized) .gallery__image a {
    height: 32.4vw;
  }
}

@media only screen and (max-width: 639px) {
  .gallery__list {
    padding-right: 50px;
  }
}

@media only screen and (max-width: 479px) {
  .gallery__image a:before {
    display: none;
  }

  .gallery__image a:after {
    display: none;
  }
}

@media only screen and (min-width: 992px) and (max-width: 1199px) {
  .gallery__list:not(.swiper-container-initialized) .gallery__item {
    width: 231px;
  }

  .gallery__list:not(.swiper-container-initialized) .gallery__item:nth-child(4n) {
    margin-right: 0;
  }

  .gallery__image a {
    height: 163px;
  }
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
  .gallery__list:not(.swiper-container-initialized) .gallery__item {
    width: 367px;
  }

  .gallery__list:not(.swiper-container-initialized) .gallery__item:nth-child(2n) {
    margin-right: 0;
  }

  .gallery__list:not(.swiper-container-initialized) .gallery__image a {
    height: 244px;
  }

  .gallery__image a {
    height: 224px;
  }
}